import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import Header from './Header';
import Results from './Results';
import { listClasses } from '../../../../api/teachers';
import useAuth from '../../../../hooks/useAuth';


const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const ClassListView = () => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [sclass, setClasss] = useState([]);
  const { user } = useAuth();
  const getClasss = useCallback(async () => {
    try {
      const sclass = await listClasses(user.userId);
      
      if (isMountedRef.current) {
        setClasss(sclass);
        console.log(sclass);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {getClasss();}, [getClasss]);
  return (
    <Page className={classes.root} title="Trường học">
      <Container maxWidth={false}>
        <Header/>
        <Box mt={3}>
          <Results sclasses={sclass}/>
        </Box>
      </Container>
    </Page>
  );
};

export default ClassListView;
