import app from './_firebase';

import {getFirestore} from 'firebase/firestore'


async function listAllTeachers() {
    try {
        let querySnapshot = await app.firestore().collection('teachers').limit(25).get();
        const teachers = querySnapshot.docs.map((doc) => {
            return { id: doc.id, ...doc.data() }
          })
          return teachers
    } catch (err) {
        console.log(err);
    }
};
/**
 * 
 * Teachers
 * status: 0: init
 *         1: active/approved
 *         2: canceled
 */
async function enroleTeachers(uids){
    uids.forEach(async uid => {
        console.log(uid)
        let querySnapshot = await app.firestore().collection('users').doc(uid).get();
        const user = querySnapshot.data()
        let m={firstname:user.firstname,lastname:user.lastname,email:user.email,status:1};
        if(typeof(user.avatar_url)!=="undefined"){
            m.avatar_url=user.avatar_url;
        }
        if(typeof(user.display_name)==="undefined"){
            m.display_name=user.lastname+" "+user.firstname;
        } else {
            m.display_name=user.display_name;
        }
        app.firestore().collection('teachers').doc(uid).set(m);
        app.firestore().collection('users').doc(uid).update({roles:getFirestore().FieldValue.arrayUnion("teacher")})
    });
    console.log("done")
}
async function listMembers(classid) {
    try {
        let querySnapshot = await app.firestore().collection('classes').doc(classid).collection('class_members').get();
        
        const members = querySnapshot.docs.map((doc) => {
            return { id: doc.id, ...doc.data() }
          })
          return members
    } catch (err) {
        console.log(err);
    }
};
async function listClasses(uid) {
    try {
        let querySnapshot = await app.firestore().collection('teachers').doc(uid).get();
        
        if(querySnapshot.exists){
            const teacher = await querySnapshot.data();
            return teacher.classes;
        }         
          else return [];
    } catch (err) {
        console.log(err);
    }
};

export {listAllTeachers,enroleTeachers,listClasses,listMembers}